@import 'variables';
@import 'fonts';

/* BASE
--------------------------------------------------------------------------
========================================================================*/

/* Reset
------------------------------------------------------------------------*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
main, article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
  font-family: $roboto-regular;
}

main {
  @include tablet {
    padding-bottom: 40px;
  }
}

p,
ul {
  @include desktop{
    color: lighten($primary-gray, 25%);
  }
}

ol, ul {
  list-style: none;
  margin-left: 0;
}

h2,
.h2 {
  font-family: $roboto-bold;
  margin-bottom: 15px;

  @include tablet {
    margin-bottom: 25px;
  }
}

h1, h2, h3, h5, h6, a {
  color: $primary-red;

  a:hover{
      color: #000;
  }
}

blockquote, q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

// IE placeholder input clear ['X'] hide

::-ms-clear {
    display: none;
}

/* 3. LAYOUT
--------------------------------------------------------------------------
========================================================================*/

/* Default styles
------------------------------------------------------------------------*/

*, *::before, *::after {
  box-sizing: border-box ;
}


ul {
  margin: 0;
  padding: 0;
}

li {
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

img.float-center {
  margin-bottom: 20px;
}

p {
  margin-bottom: 15px;

  & + table {
    margin-top: 25px;
  }
}

/* Abrr */
abbr[title],
abbr[data-original-title] {
  border: 0;
}

/* Utilities */
.relative {
  position: relative;
}

.uppercase {
  text-transform: uppercase;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.display-inline {
  display: inline;
}

.block {
  display: block;
}

.inline-block {
  display: inline-block;
}

.custom-hide {
  display: none;
}

.children-inline-block {
  & > * {
    display: inline-block;
  }

  &.vcentered {
    & > * {
      vertical-align: middle;
    }
  }

  &.bottom-centered {
    & > * {
      vertical-align: text-bottom;
    }
  }
}

.clear-both {
  clear: both;
}

.overflow-visible {
  overflow: visible !important;
}

.margin-bottom-small {
  margin-bottom: 30px;
}

.margin-top-large {
  margin-top: 40px;
}

.margin-auto {
  margin: auto;
}

.picture-hover {

  &:hover {
    transform: scale(1.1);
  }
}

ul {
  -webkit-padding-start: 0px;
}

footer {
  background-color: $primary-gray;
  color: $primary-red;

  ul {
    color: #fff;
  }

  .copy {
    background-color: lighten($primary-gray, 5%);
  }
}
h3,
.h3,
h2,
a {
  color: $primary-red;
}

.margin-top-small {
  margin-top: 10px !important;
}

.margin-top-medium {
  margin-top: 40px !important;
}

.padding-up-down {
  padding: 30px 0;
}

.vertical-center {
  @media (min-width: 768px) {
    display: flex;
    align-items: center;

  }
}
